@import "./themes.scss";

#skills {
  @include BackgroundDotted;
}

#skills-main {
  padding: 50px 30px;
  max-width: 1440px;
  margin: 0 auto;
}

.skill {
  transition: box-shadow 0.3s ease;
  transition: width 0.5s ease;
  border-radius: 10px;
  background: var(--backgroundRadialNoise);
  margin: 10px 20px;
  width: 350px;
  height: 500px;
  text-align: center;
  align-content: space-between;
  transition: box-shadow 0.3s ease;
  &:hover {
    box-shadow: var(--boxShadow3D);
  }
}

.skills-description {
  text-align: center;
}

.skills-description-title {
  margin: 0 auto;
  margin-bottom: 20px;
  max-width: 500px;
  font-weight: 800;
  color: var(--textColor01);
  font-size: 1.7rem;
}

.skills-description-text {
  max-width: 800px;
  margin: 0 auto;
}

.skills-container {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  max-width: 1600px;
}

.skill-img {
  filter: var(--invert);
  opacity: 0.7;
padding: 25px;
  transition: transform 0.3s ease, opacity 0.3s ease;
  height: 50%;
  margin: 0 auto;
  margin-bottom: 30px;
  &:hover {
    transform: scale(1.05);
  }
}

.skill-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--textColor01);
}

.skill-text {
  color: var(--textColor02);
  font-size: 0.9rem;
  max-width: 300px;
  margin: 0 auto;
}

.skill-number {
  color: var(--textColor02);
  text-align: left;
  width: 80%;
  margin: 20px auto;
}

#accordion-menu {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  grid-area: 1 / 1 / 2 / 2;
}

#accordion {
  max-width: 1200px;
  margin: 0 auto;
  height: 600px;
  justify-items: center;
  align-items: center;
}

.accordion-image-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  align-items: center;
  align-content: center;
}

.accordion-image-container-extra {
  overflow: hidden;
  justify-items: center;
  align-content: center;
  // height: 400px;
}

.accordion-image-extra {
  margin: 0 auto 20px auto;
}

.accordion-image {
  width: 100%;
  margin: 0 auto;
  grid-area: 1 / 2 / 2 / 3;
}

@media screen and (max-width: 1070px) {
  #accordion {
    display: block;
    // padding: 60px 30px 60px 30px;
    height: auto;
  }
  .accordion-item {
    width: 100%;
  }
  .accordion-image-container-extra {
    display: block;
  }
  .accordion-image-container {
    display: none;
  }
  .accordion-description-container {
    display: block;
  }
}

@media screen and (min-width: 1071px) {
  #accordion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }
  .accordion-item {
    width: 400px;
  }
  .accordion-description-container {
    width: auto;
    display: inline-block;
  }
  .accordion-image-container-extra {
    display: none;
  }
  .accordion-image-container {
    display: inline-block;
  }
}
